import 'component-simple-slider';
      import { html } from 'lit-element';
      import ContentService from '/services/content-service.js';

      const slides = ContentService.getContent('home');
      const simpleSlider = document.getElementsByTagName('x-simple-slider')[0];
      const slidesContent = slides.reduce((acc, item, index) => {
        return acc + `
          <div slot="slide${index + 1}"><h3>${item.title}</h3>${item.template}</div>
        `;
      }, '');

      simpleSlider.setAttribute('slots', slides.length);
      simpleSlider.innerHTML = slidesContent;
    
